.companyInfoModal {
  position: fixed;
  z-index: 500;
  background-color: #f5f5f5;
  left: 30%;
  top: 20%;
  padding: 20px;
  border: 2px solid #9f9f9f;
  min-width: 150px;
  max-width: 250px;
  text-align: left;
}
.copmanyInfoModalHeader {
  font-weight: 700;
  margin: 0 0 5px;
}
.companyInfoModal .copmanyInfoModalSubheader {
  font-size: 1.13em;
  font-weight: 500;
  margin: 0 0 5px;
}
.companyInfoModal.content {
  position: relative;
  width: 100%;
  height: 100%;
}
.companyInfoModal .close-btn {
  position: absolute;
  padding: 3px;
  right: 0;
  top: 0;
  font-size: 1.1em;
  background-color: #fff0;
  border: none;
  text-shadow: 0 1px 0 #fff;
}
.companyInfoModal .close-btn img {
  width: 20px;
  height: auto;
  border: none;
}
.companyInfoModal .close-btn img:hover {
  transform: scale(1.1)
}
.companyInfoModal .close-btn:hover {
  font-weight: 700;
  cursor: pointer;
}
.companyInfoModal .close-btn:focus {
  outline: none;
}

.companyInfoModal .imageContainer {
  overflow: hidden;
  margin: 0;
}
.companyInfoModal .imageContainer img {
  width: 100%;
  height: auto;
}

.companyInfoModal .attribute-descr {
  color: #7f7f7f;
  font-size: 0.85em;
  font-style: italic;
  margin: 18px 0 0 0;
}

.companyInfoModal p {
  text-align: left;
  margin: 0px 0 0 7px;
}

@media(max-width: 600px) {
  .companyInfoModal {
    position: fixed;
    left: auto;
    top: 10vh;
    max-width: 200px;
  }
}
