.scheme {
  overflow: auto;
  position: relative;
  margin: 0 20px 20px;
  border: 1px solid black;
  max-height: 80vh;
}
.scheme svg {
  width: 1100px;
  overflow: auto;
}

.scheme::-webkit-scrollbar { 
  display: none; 
}

.scheme-container .scale-btn-container {
  left: 10px;
  position: absolute;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.scheme-container .scale-btn-plus, .scheme-container .scale-btn-minus {
  position: absolute;
  margin-left: 35px;
  z-index: 30;
  background-color: #c9212103;
  width: 50px;
  height: 50px;
  border: none;
  padding: 0;
}
.scheme-container .scale-btn-plus:hover, .scheme-container .scale-btn-minus:hover {
  transform: scale(1.05);
}
.scheme-container .scale-btn-plus:focus, .scheme-container .scale-btn-minus:focus {
  outline: none;
}

.scale-btn-plus {
  top: 30px;
}
.scale-btn-minus {
  top: 100px;
}

/* .scheme {overflow: hidden;} */
#scheme-0, #scheme-1, #scheme-2 {
  /* overflow: auto; */
}
@media(max-width: 600) {
  .scheme {
    overflow: auto;
  }
}


