.footer {
  padding: 10px 20px;
  text-align: start;
  background-color: #dfdfdf;
}

.footer-item {
  display: inline-flex;
  align-items: center;
  width: 33%;
  padding-top: 12px;
}
.footer-item img{
  width: 30px;
  height: auto;
  border: 2px solid black;
  border-radius: 3px;
  margin-right: 10px;
}

@media (max-width: 600px) {
  .footer-item {
    width: 50%;
  }
}
@media (min-width: 1400px) {
  .footer {
    padding: 10px 400px;
  }
  .footer-header {
    text-align: center;
    margin-right: 100px;
  }
  .footer-item {
    padding-left: 20px;
    width: 30%;
  }
}