body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  margin-top: 10px;
}

.App button:focus {
  outline: none;
}

.App .main-header {
  margin: 0 0 21px; 
}

@media (min-width: 650px) {
  .App main {
    margin: 0 5px;
  }
}
@media (min-width: 1400px) {
  .App main {
    margin: 0 15px;
  }
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App button {
  cursor: pointer;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.fade-enter {
  opacity: 0.01;
}

.fade-enter-active {
  opacity: 1;
  -webkit-transition: all 300ms ease-in;
  transition: all 300ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-out;
  transition: opacity 300ms ease-out;
}
.scheme {
  overflow: auto;
  position: relative;
  margin: 0 20px 20px;
  border: 1px solid black;
  max-height: 80vh;
}
.scheme svg {
  width: 1100px;
  overflow: auto;
}

.scheme::-webkit-scrollbar { 
  display: none; 
}

.scheme-container .scale-btn-container {
  left: 10px;
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}
.scheme-container .scale-btn-plus, .scheme-container .scale-btn-minus {
  position: absolute;
  margin-left: 35px;
  z-index: 30;
  background-color: #c9212103;
  width: 50px;
  height: 50px;
  border: none;
  padding: 0;
}
.scheme-container .scale-btn-plus:hover, .scheme-container .scale-btn-minus:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.scheme-container .scale-btn-plus:focus, .scheme-container .scale-btn-minus:focus {
  outline: none;
}

.scale-btn-plus {
  top: 30px;
}
.scale-btn-minus {
  top: 100px;
}

