.filtersContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0 20px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.categoriesFilter, .brandsFilter, .levelsFilter {
  width: 47%;
  margin-bottom: 20px;
  max-height: 300px;
}
.filterHeader, .levelsFilter {
  height: 40px;
  background-color: #DFDFDF;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.filterHeader {
  border-radius: 3px 3px 0 0;
  font-size: 18px;
}
.levelsFilter {
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 45px;
}
.levelsFilterHeader {
  padding-left: 10px;
  width: 100px;
  font-size: 18px;
  text-align: left;
}
.levelsFilter_buttons-container {
  margin-left: 10px;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  position: relative;
}
.levelsFilter button {
  width: 33%;
  font-weight: 700;
  background-color: #ffffff;
  border: 1px solid #afafaf;
}
.levelsFilter button:focus {
  outline: none;
}
.levelsFilter .selectedLevel {
  border: 2px solid black;
  background-color: #6ad12e;
}
.filterIcon {
  width: 50px;  
  height: 30px;
}
.filterIcon img {
  width: 30px;
}

@media(max-width: 600px) {
  .categoriesFilter, .brandsFilter, .levelsFilter {
    width: 99.5%; 
  }
  .filtersContainer {
    -webkit-justify-content: center;
            justify-content: center;
  }
}
@media(min-width: 1400px) {
  .categoriesFilter, .brandsFilter, .levelsFilter {
    width: 48%;
    margin-bottom: 20px;
    max-height: 300px;
  }
}
.footer {
  padding: 10px 20px;
  text-align: start;
  background-color: #dfdfdf;
}

.footer-item {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  width: 33%;
  padding-top: 12px;
}
.footer-item img{
  width: 30px;
  height: auto;
  border: 2px solid black;
  border-radius: 3px;
  margin-right: 10px;
}

@media (max-width: 600px) {
  .footer-item {
    width: 50%;
  }
}
@media (min-width: 1400px) {
  .footer {
    padding: 10px 400px;
  }
  .footer-header {
    text-align: center;
    margin-right: 100px;
  }
  .footer-item {
    padding-left: 20px;
    width: 30%;
  }
}
.companyInfoModal {
  position: fixed;
  z-index: 500;
  background-color: #f5f5f5;
  left: 30%;
  top: 20%;
  padding: 20px;
  border: 2px solid #9f9f9f;
  min-width: 150px;
  max-width: 250px;
  text-align: left;
}
.copmanyInfoModalHeader {
  font-weight: 700;
  margin: 0 0 5px;
}
.companyInfoModal .copmanyInfoModalSubheader {
  font-size: 1.13em;
  font-weight: 500;
  margin: 0 0 5px;
}
.companyInfoModal.content {
  position: relative;
  width: 100%;
  height: 100%;
}
.companyInfoModal .close-btn {
  position: absolute;
  padding: 3px;
  right: 0;
  top: 0;
  font-size: 1.1em;
  background-color: #fff0;
  border: none;
  text-shadow: 0 1px 0 #fff;
}
.companyInfoModal .close-btn img {
  width: 20px;
  height: auto;
  border: none;
}
.companyInfoModal .close-btn img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1)
}
.companyInfoModal .close-btn:hover {
  font-weight: 700;
  cursor: pointer;
}
.companyInfoModal .close-btn:focus {
  outline: none;
}

.companyInfoModal .imageContainer {
  overflow: hidden;
  margin: 0;
}
.companyInfoModal .imageContainer img {
  width: 100%;
  height: auto;
}

.companyInfoModal .attribute-descr {
  color: #7f7f7f;
  font-size: 0.85em;
  font-style: italic;
  margin: 18px 0 0 0;
}

.companyInfoModal p {
  text-align: left;
  margin: 0px 0 0 7px;
}

@media(max-width: 600px) {
  .companyInfoModal {
    position: fixed;
    left: auto;
    top: 10vh;
    max-width: 200px;
  }
}

