.filtersContainer {
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  flex-wrap: wrap;
}
.categoriesFilter, .brandsFilter, .levelsFilter {
  width: 47%;
  margin-bottom: 20px;
  max-height: 300px;
}
.filterHeader, .levelsFilter {
  height: 40px;
  background-color: #DFDFDF;
  display: flex;
  align-items: center;
}
.filterHeader {
  border-radius: 3px 3px 0 0;
  font-size: 18px;
}
.levelsFilter {
  justify-content: space-between;
  height: 45px;
}
.levelsFilterHeader {
  padding-left: 10px;
  width: 100px;
  font-size: 18px;
  text-align: left;
}
.levelsFilter_buttons-container {
  margin-left: 10px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  position: relative;
}
.levelsFilter button {
  width: 33%;
  font-weight: 700;
  background-color: #ffffff;
  border: 1px solid #afafaf;
}
.levelsFilter button:focus {
  outline: none;
}
.levelsFilter .selectedLevel {
  border: 2px solid black;
  background-color: #6ad12e;
}
.filterIcon {
  width: 50px;  
  height: 30px;
}
.filterIcon img {
  width: 30px;
}

@media(max-width: 600px) {
  .categoriesFilter, .brandsFilter, .levelsFilter {
    width: 99.5%; 
  }
  .filtersContainer {
    justify-content: center;
  }
}
@media(min-width: 1400px) {
  .categoriesFilter, .brandsFilter, .levelsFilter {
    width: 48%;
    margin-bottom: 20px;
    max-height: 300px;
  }
}